.container {
  padding-right: 15px;
  padding-left: 15px;
    margin-right: auto;
    margin-left: auto;
}
// .home-container{
//   padding-right: 15px;
//   padding-left: 15px;
//   margin-right: auto;
//   margin-left: auto;
// }
  @media (min-width: 1600px) {
      .container {
          max-width: 1600px;
      }
  }
  @media (min-width: 1900px) {
      .container {
          max-width: 1900px;
      }
  }
  @media (min-width: 768px) {
    .container {
      width: 750px;
    }
  }
  @media (min-width: 992px) {
    .container {
      width: 970px;
      padding-right: 2rem;
      padding-left: 2rem;
    }
    // .home-container{
    //   padding-right: 6rem;
    //   padding-left: 6rem;
    // }
  }
  @media (min-width: 1200px) {
    .container {
      width: 1180px;
    }
  }
  .container-fluid {
    padding-right: 15px;
    padding-left: 15px;
    width: 100%;
    margin-right: auto;
    margin-left: auto;
  }